body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Title container */
.page-title {
  text-align: center;
  color: #333; /* Darker text for better contrast */
}

/* Main title styling */
.page-title h1 {
  font-weight: 700; /* Make the main title bold */
  color: #4CAF50; /* Green color for a refreshing look */
  margin-bottom: 5px; /* Space between title and subtitle */
  text-transform: uppercase; /* Uppercase for emphasis */
  letter-spacing: 1px; /* Spacing between letters */
  line-height: 1.2; /* Better line spacing */
}

/* Subtitle styling */
.page-title h5 {
  font-size: 1.2em;
  font-weight: 400;
  color: #666; /* Lighter color for subtitle */
  font-style: italic; /* Italicize the subtitle */
  margin-top: 0; /* Reduce gap after title */
  margin-bottom: 15px; /* Increase gap after title */
  letter-spacing: 0.5px; /* Slightly wider letter spacing */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .page-title h1 {
    font-size: 2.4em; /* Adjust font size for smaller screens */
  }
  .page-title h5 {
    font-size: 1em; /* Adjust subtitle size */
  }
}

