/* Container for no results */
.no-results-container {
  text-align: center;
  padding: 40px 20px;
  margin: 20px auto;
  max-width: 600px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Main no results message */
.no-results-message {
  font-size: 1.8em;
  font-weight: 600;
  color: #4CAF50; /* Highlighting with the same color theme */
  margin-bottom: 10px;
}

/* Suggestion for user */
.no-results-suggestion {
  font-size: 1em;
  font-weight: 400;
  color: #666;
  margin-top: 0;
}

/* Add responsiveness */
@media (max-width: 768px) {
  .no-results-message {
    font-size: 1.5em;
  }
  .no-results-suggestion {
    font-size: 0.9em;
  }
}
